body {
    margin: 0;
  }
  
  #root {
    padding: 15px;
  }

  form {
    padding: 5px;
    width: 300px;
    border: 1px solid black;
  }
  
  input {
    display: block;
    width: 150px;
    margin: 5px 0;
  }
  
  form button {
    display: block;
  }
  
  button {
    margin: 25px 5px;
    background-color: #F5F4FA;
    color: #212121;
    font-weight: 700;
    font-size: 16px;
    line-height: 26px;
    text-align: center;
    letter-spacing: 0.03em;
    border-radius: 50px;
    padding-top: 6px;
    padding-right: 22px;
    padding-bottom: 6px;
    padding-left: 22px;
    cursor: pointer;
    border: 1px solid rgba(255, 255, 255, 0.3);
    outline-style: none;
  }

  button:hover  {
    color: #F5F4FA;
    background-color: #2196F3;
    box-shadow: 1px 2px 10px rgba(0, 0, 0, 0.3);
    transition: 250ms cubic-bezier(0.4, 0, 0.2, 1);;
  }